import Deployer from './Deployer'

export default class Supervisor {
  constructor() {
    this.deployers = []
  }

  get elements() {
    return this.deployers.map(d => d.element)
  }

  async deploy(element) {
    let deployer = new Deployer(element)
    this.deployers.push(deployer)

    try {
      await deployer.run()
    } catch (error) {
      element.status = 'Failed'
      element.error = error
    }
  }
}
